.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav a {
  text-align: left;
  text-decoration: none;
  color: white;
}

.nav a:active {
    color: #a0a0a0;
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 1rem;
  color: white;
}

.site-title {
  font-size: 2rem;
  padding: 10px;
}

.nav li {
    margin: .25rem;
    padding: .5rem;
}

.nav li:hover {
    transform: scale(1.2);
    transition: all 0.2s ease, left 0.2s ease, right 0.3s ease;
}

.nav li:not(:hover) {
    transform: scale(1);
    transition: all 0.3s ease;
}

.site-title:hover {
    border-radius: 5px;
    transform: scale(1.025);
    transition: all 0.2s ease;
  }
  
.site-title:not(:hover) {
      transform: scale(1);
      transition: all 0.3s ease;
}

.gradient-container {
    width: 100%;
    height: 300%;
}

.homecontainer {
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
    background-color: rgba(0, 0, 0, 0.212);
    color: white;
    display: grid;
    grid-template-columns: 1fr, 1fr, 1fr;
    grid-template-rows: 1fr, 1fr, 1fr;
    border-radius: 15px;
    position: relative;
    overflow: hidden;
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }
  
.homecontainer:before {
    position: absolute;
    background: inherit;
}

.homesection {
    grid-column: 2;    
}